import emojilib from "emojilib";

const input_colon = false;
const colon = /:([^\s]*):/gi;

document.body.addEventListener("input", (event) => {
	Array.from(document.querySelectorAll("input, textarea")).forEach((input) => {
		Array.from(input.value.matchAll(colon)).forEach((word) => {
			const emojiName = word[1];
			const emoji = emojilib.lib[emojiName];
			if (emoji) {
				input.value = input.value.replace(word[0], emoji.char);
			}
		});
	});

	Array.from(document.querySelectorAll("*[contenteditable]")).forEach(
		(input) => {
			Array.from(input.textContent.matchAll(colon)).forEach((word) => {
				const emojiName = word[1];
				const emoji = emojilib.lib[emojiName];
				if (emoji) {
					input.textContent = input.textContent.replace(word[0], emoji.char);
				}
			});
		}
	);
});
